___CSS_LOADER_EXPORT___.push([module.id, "/*====== \nVariables \n======\n*/\n:root {\n    --primaryLightColor: hwb(0 50% 50%);\n    --primaryColor: #000000;\n    --primaryDarkColor: #c02c03;\n    --mainWhite: #fff;\n    --offWhite: #f7f7f7;\n    --mainBackground: #f3f3f3;\n    --mainOverlay: rgba(35, 10, 36, 0.4);\n    --mainBlack: #222;\n    --mainGrey: #ececec;\n    --darkGrey: #afafaf;\n    --mainRed: #bd0303;\n    --mainTransition: all 0.3s linear;\n    --lightShadow: 1px 3px 3px 0px rgba(0, 0, 0, 0.5);\n    --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);\n    --mainBorderRadius: 0.25rem;\n    --smallWidth: 85vw;\n    --maxWidth: 40rem;\n    --fullWidth: 1170px;\n  }\n  /* \n  ====== \n  Global Styles \n  ======\n  */\n  * {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n  }\n  \n  body {\n    font-family:'Helvetica Neue';\n    color: var(--mainBlack);\n    background: var(--mainBackground);\n    line-height: 1.4;\n    font-size: 1rem;\n    font-weight: 300;\n    z-index: 999;\n  }\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6 {\n    font-family: 'Helvetica Neue';\n    margin-bottom: 1.25rem;\n\n  }\n  p {\n    margin-bottom: 1.25rem;\n  }\n  ul {\n    list-style-type: none;\n  }\n  a {\n    text-decoration: none;\n    color: var(--mainBlack);\n  }\n  img {\n    width: 100%;\n    display: block;\n  }\n  \n  /* \n  ====== \n  Buttons \n  ======\n  */\n  .btn,\n  .btn-white,\n  .btn-primary {\n    text-transform: uppercase;\n    color: var(--primaryColor);\n    border: 2px solid var(--primaryColor);\n    padding: 0.45rem 0.8rem;\n    display: inline-block;\n    transition: var(--mainTransition);\n    cursor: pointer;\n    font-size: 0.8rem;\n    background: transparent;\n    border-radius: var(--mainBorderRadius);\n    display: inline-block;\n  }\n  .btn:hover {\n    background: var(--primaryColor);\n    color: var(--mainWhite);\n  }\n  .btn-white {\n    background: transparent;\n    color: var(--mainWhite);\n    border-color: var(--mainWhite);\n  }\n  .btn-white:hover {\n    background: var(--mainWhite);\n    color: var(--primaryColor);\n  }\n  .btn-primary {\n    background: var(--primaryColor);\n    color: var(--mainWhite);\n    border-color: transparent;\n  }\n  .btn-primary:hover {\n    background: var(--primaryLightColor);\n    color: var(--primaryColor);\n  }\n  .btn-block {\n    width: 100%;\n    display: block;\n    margin: 0 auto;\n    box-shadow: var(--lightShadow);\n    text-align: center;\n  }\n  .btn-details {\n    padding: 0.25rem 0.4rem;\n  }\n  .btn-details:hover {\n    background: var(--primaryLightColor);\n    border-color: var(--primaryLightColor);\n  }\n  /* \n  ====== \n  Navbar\n  ======\n  */\n  .navbar {\n    background: var(--mainWhite);\n    height: 5rem;\n    display: flex;\n    align-items: center;\n    border-bottom: 1px solid var(--primaryColor);\n    box-shadow: var(--lightShadow);\n    font-family: 'Helvetica Neue';\n  }\n  .nav-center {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    width: var(--smallWidth);\n    margin: 0 auto;\n    max-width: var(--fullWidth);\n  }\n  .nav-links {\n    display: flex;\n    align-items: center;\n  }\n  .nav-links a {\n    text-transform: capitalize;\n    display: inline-block;\n    font-weight: bold;\n    margin-right: 0.5rem;\n    font-weight: 400;\n    font-size: 1.2rem;\n    padding: 0.25rem 0.5rem;\n    transition: var(--mainTransition);\n  }\n  .nav-links a:hover {\n    color: var(--primaryColor);\n  }\n  .logo {\n    width: 12rem;\n    color: rgb(105, 2, 2);\n  }\n  /* \n  ====== \n  About\n  ======\n  */\n  .about-section {\n    width: var(--smallWidth);\n    max-width: var(--maxWidth);\n    margin: 0 auto;\n    font-family: 'Helvetica Neue';\n  }\n  .about-section p {\n    line-height: 2rem;\n    font-weight: 400;\n  }\n  /* \n  ====== \n  Error\n  ======\n  */\n  .error-page {\n    display: flex;\n    justify-content: center;\n  }\n  .error-container {\n    text-align: center;\n    text-transform: capitalize;\n  }\n  \n  section {\n      padding: 5rem 0;\n    }\n    .section-title {\n      font-size: 2rem;\n      text-transform: capitalize;\n      text-align: center;\n      margin-bottom: 3.5rem;\n      margin-top: 1rem;\n    }\n  \n    /*\n    ======\n    Loading\n    ========\n    */\n    .loader,\n  .loader:before,\n  .loader:after {\n    background: transparent;\n    -webkit-animation: load1 1s infinite ease-in-out;\n    animation: load1 1s infinite ease-in-out;\n    width: 1em;\n    height: 4em;\n  }\n  .loader {\n    color: var(--primaryColor);\n    text-indent: -9999em;\n    margin: 88px auto;\n    margin-top: 20rem;\n    position: relative;\n    font-size: 3rem;\n    -webkit-transform: translateZ(0);\n    -ms-transform: translateZ(0);\n    transform: translateZ(0);\n    -webkit-animation-delay: -0.16s;\n    animation-delay: -0.16s;\n  }\n  .loader:before,\n  .loader:after {\n    position: absolute;\n    top: 0;\n    content: '';\n  }\n  .loader:before {\n    left: -1.5em;\n    -webkit-animation-delay: -0.32s;\n    animation-delay: -0.32s;\n  }\n  .loader:after {\n    left: 1.5em;\n  }\n  @-webkit-keyframes load1 {\n    0%,\n    80%,\n    100% {\n      box-shadow: 0 0;\n      height: 4em;\n    }\n    40% {\n      box-shadow: 0 -2em;\n      height: 5em;\n    }\n  }\n  @keyframes load1 {\n    0%,\n    80%,\n    100% {\n      box-shadow: 0 0;\n      height: 4em;\n    }\n    40% {\n      box-shadow: 0 -2em;\n      height: 5em;\n    }\n  }\n  \n  /* \n  ====== \n  Cocktail\n  ======\n  */\n  \n  .cocktail-section {\n    text-align: center;\n    font-family: 'Helvetica Neue', sans-serif;\n    color: #212121;\n    }\n    \n    .cocktails-center {\n    display: flex;\n    flex-wrap: wrap;\n    height: 100%;\n    max-width: 70rem;\n    margin: 0 auto;\n    height: 100%;\n    margin: 0 auto;\n    align-items: center;\n    justify-content: center;\n    box-shadow: rgb(38, 38, 38);\n    }\n    \n    .drink {\n    width: 17rem;\n    height: 25rem;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-direction: column;\n    box-shadow: rgb(38, 38, 38);\n    border-radius: 10px;\n    margin: 1.5rem 1.5rem;\n    background: #fff;\n    }\n    \n    .drink-img {\n    width: 100%;\n    height: 65%;\n    }\n    \n    .drink-img img {\n    display: block;\n    width: 100%; height: 100%;\n    object-fit: cover;\n    }\n    \n    .drink-footer {\n    height: 25%;\n    width: 100%;\n    text-align: center;\n    padding: 0.5rem 0;\n    font-size: 15px;\n    color: #666;\n    }\n    \n    .drink p, .drink h3, .drink h4{\n    margin: 0; padding: 0;\n    font-weight: lighter;\n    font-size: 1rem;\n    }\n  \n  /*\n  ====== \n  SearchForm\n  ======\n  */\n  .search {\n    margin-top: 1rem;\n    padding-bottom: 0;\n\n    }\n    .search-form {\n      width: var(--smallWidth);\n      margin: 0 auto;\n      max-width: var(--maxWidth);\n      background: var(--mainWhite);\n      padding: 2rem 2.5rem;\n      text-transform: capitalize;\n      border-radius: var(--mainBorderRadius);\n      box-shadow: var(--lightShadow);\n    }\n    \n    .form-control label {\n      display: block;\n      margin-bottom: 1.25rem;\n      font-weight: 200;\n      color: var(--primaryColor);\n    }\n    .form-control input {\n      width: 100%;\n      border: none;\n      border-color: transparent;\n      background: var(--mainBackground);\n      border-radius: var(--mainBorderRadius);\n      padding: 0.5rem;\n      font-size: 1.2rem;\n    }\n\n  \n  /*\n  ================\n  SingleCocktail\n  */\n  .singlecocktail {\n    width: 25rem;\n    height: 45rem;\n    margin:0 auto;}\n  \n  .singledrink {\n    margin: 0 auto;\n    height: 30rem;\n  }\n  \n    /* Estilo para el contenedor principal */\n  .jumbotron {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      margin-top: 5px;\n      margin-bottom: 1px;\n      position: relative;\n      z-index: 1;\n      width: 100%;\n      }\n      \n      /* Estilo para el contenedor interno */\n  .container-button-random-cocktail {\n      width: 20%;\n      max-width: 1200px;\n      padding: 15px;\n      background-color: white;\n      border-radius: 8px;\n      box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.2);\n      margin-top: 0px;\n      margin-bottom: auto;\n      position: relative;\n      z-index: 2;\n      }\n      \n      /* Estilo para el botn */\n  .btn-secondary {\n      font-size: 20px;\n      font-family:'Helvetica Neue' ;\n      font-weight: 300;\n      border-radius: 4px;\n      background-color: #007AFF;\n      color: white;\n      cursor: pointer;\n      }\n      \n      /* Estilo para el botn al hacer hover */\n  .btn-secondary:hover {\n      background-color: #006DF0;\n      }\n      \n      /* Estilo para el card */\n  .card {\n        width: 25rem;\n        height: 15rem;\n        top:60%;\n        bottom: 40%;\n        left:48%;\n        transform:translate(-50%,-50%);\n        display: fixed;\n        justify-content: center;\n        box-shadow: rgb(38, 38, 38);\n        border-radius: 10px;\n        margin: 1.5rem 1.5rem;\n        background: #fff;\n        position: absolute;\n        z-index: 500;\n        }\n  \n   .overlay {\n          background-color: rgba(0, 0, 0,0.5); /* or any other color of your choice */\n          position: fixed;\n          top: 0;\n          left: 0;\n          width: 100%;\n          height: 100%;\n          z-index: 1000;\n        }\n        \n\n  /* Estilo para el contenedor del card */\n/* Estilo para el contenedor del card */\n  .card .card-dialog {\n    max-width: 400px;\n    width: 50%;\n    max-height: 400px;\n    height: 50px;\n    }\n\n\n    /* Estilo para el contenido del card */\n    .card .card-content {\n      background-color: white;\n      border-radius: 8px;\n      box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.2);\n      }\n      \n      /* Estilo para el encabezado del card */\n      .card .card-header {\n      background-color: #F5F5F5;\n      border-bottom: 1px solid #E0E0E0;\n      border-top: 1px solid #E0E0E0 ;\n      padding: 16px;\n      }\n      \n      /* Estilo para el ttulo del card */\n      .card .card-header h5 {\n      font-size: 18px;\n      font-weight: bold;\n      margin: 0;\n      }\n      \n      /* Estilo para el botn de cierre del card */\n      .card .card-header .close {\n      font-size: 18px;\n      color: #BDBDBD;\n      cursor: pointer;\n      }\n      \n      /* Estilo para el cuerpo del card */\n      .card .card-body {\n      padding: 16px;\n      }\n      \n      /* Estilo para la imagen del card */\n      .card .card-body img {\n      max-width: 100%;\n      border-radius: 8px;\n      }\n      \n      /* Estilo para las instrucciones del card */\n      .card .card-body p:first-of-type {\n      margin-top: 16px;\n      font-size: 16px;\n      }\n      \n      /* Estilo para la lista de ingredientes del card */\n      .card .card-body ul {\n      margin-top: 16px;\n      font-size: 16px;\n      list-style: none;\n      }\n      \n      /* Estilo para el pie del card */\n      .card .card-footer {\n      background-color: #F5F5F5;\n      border-top: 1px solid #E0E0E0;\n      padding: 16px;\n      text-align: right;\n      }\n      \n      /* Estilo para el botn de cierre del pie del card */\n      .card .card-footer button {\n      font-size: 16px;\n      padding: 8px 16px;\n      border-radius: 4px;\n      background-color: #007AFF;\n      color: white;\n      cursor: pointer;\n      }\n      \n      /* Estilo para el botn de cierre del pie del card al hacer hover */\n      .card .card-footer button:hover {\n      background-color: #006DF0;\n      }\n", "",{"version":3,"sources":["webpack://./src/index.css"],"names":[],"mappings":"AAAA;;;CAGC;AACD;IACI,mCAAmC;IACnC,uBAAuB;IACvB,2BAA2B;IAC3B,iBAAiB;IACjB,mBAAmB;IACnB,yBAAyB;IACzB,oCAAoC;IACpC,iBAAiB;IACjB,mBAAmB;IACnB,mBAAmB;IACnB,kBAAkB;IAClB,iCAAiC;IACjC,iDAAiD;IACjD,iDAAiD;IACjD,2BAA2B;IAC3B,kBAAkB;IAClB,iBAAiB;IACjB,mBAAmB;EACrB;EAC